<div class="ui error message mobile-error">please play game from desktop</div><div class="gamearea"><div id="score"><div class="ui label huge">Score: {{ score }} </div><div class="ui huge circular label bCoins">{{coins}}</div><h3 class="ui heading red">Avoid</h3><img class="rule-images" v-for="img in mq_images" :src="require('../assets/' + img+'.png')"></div><div id="gameArea" :class="{'active':!gamepaused}"><div id="obst"></div><div class="overlay" v-show="gamepaused"><ul class="gameSummary"> <li><b>Score</b><span>{{ p_score }}</span></li><li><b>Coins</b><span>{{ p_coins }}</span></li><li><b>Total Coins</b><span>{{ $root.total_coins }}</span></li></ul><button class="startbutton ui primary large button" autofocus v-on:click="startGame()">{{ startButton}}</button></div><div id="player" :style="'left:'+player + 'px'"></div></div></div><section class="ui segment" id="rules"><h2 class="ui header">How to play</h2><ul><li>Play our simple single-player game to collect coins as Popeye runs endlessly dodging a list of food obstacles and collecting coins.</li><li>Help Popeye collect as many coins in the pursuit of avoiding tempting food obstacles.</li><li>Higher the number of coins won yields in achievement badges unlocked.</li><li>Winning 100 BCoins would lead to winning a Bronze badge and further coins collection will unlock Silver, Gold and Platinum badges.</li><li>Win exciting discounts on MRP by collecting max coins and unlocking new badges along with the exhilarating benefits helping you and your family maintain your ideal health and fitness quotient.</li></ul></section>