<div class="ui input field"><input class="ui input" placeholder="search" v-model="searchTerm"></div><nav class="catList" aria-label="category list"><div class="ui list" v-for="c in catlist" :key="c.id"><div class="item heading" :class="{'hide':c.fil.length==0}">{{ c.name }}</div><router-link class="item" :to="{name:'plp', params:{id:cs.id}}" v-for="cs in c.fil" :key="cs.id">{{ cs.name || cs.id }}</router-link></div></nav>