<div class="myaccount"><h1 class="ui header">Welcome, {{ user.firstName }}</h1><button class="ui tiny primary button" v-on:click="logout()">Logout</button><div class="ui cards accounts-cards"><div class="ui card"><h2 class="ui header teal">Current Progress</h2><div class="ui statistics"><div class="ui statistic"><span class="value"> <i class="ui huge label circular" :class="(coins&gt;0)?'bCoins':'disabled'">$</i></span><span class="label">{{ coins || 0 }} Coins</span></div><div class="ui statistic"><span class="value"> <i class="ui huge label circular badges" :class="badges"><i class="icon ui trophy" :class="{'disabled':!badges}"></i></i></span><span class="label">{{ badges || 'no' }} Badge</span></div></div><router-link class="ui primary button" to="/game">Earn more coins</router-link></div><div class="ui card checkbox"><h2 class="ui header teal">My Preferred Mealecules</h2><form class="ui form preffered_mq"><div class="ui field" v-for="m in MQ_list"><div class="ui checkbox"></div><input type="checkbox" :id="'mq_'+m" v-model="user_pm" :value="m" :disabled="user_pm.length&gt;=4 &amp;&amp; user_pm.indexOf(m) == -1"><label :for="'mq_'+m">{{m}}</label></div></form><button class="ui primary fluid button" v-on:click="savePM()" :disabled="form_disabled || loaders.pm" :class="{'loading':loaders.pm}"> Save   <div class="ui circular label tiny bCoins">10</div></button></div><div class="ui card userinfo"><h2 class="ui header teal">Update Information</h2><form class="ui form"><div class="ui field"><label>Weight (in kg)</label><input v-model="user.weight"></div><div class="ui field"><label>Height (in cm)</label><input v-model="user.height"></div><div class="ui field"><label>Age</label><input v-model="user.age"></div><button class="ui primary fluid button" :disabled="inform_disabled || loaders.info" v-on:click="saveVitals()" :class="{'loading':loaders.info}"> Save   <div class="ui circular label tiny bCoins">20</div></button></form></div><div class="ui card" v-if="badges"><div class="ui top attached label" :class="badges">{{ badges }}</div><h2 class="ui header teal">BMI Calculator</h2><form class="ui form"><div class="ui field"><label>Weight (in kg)</label><input v-model="bmi.w"></div><div class="ui field"><label>Height (in cm)</label><input v-model="bmi.h"></div><button class="ui primary fluid button" v-on:click="calcBMI()">Calculate BMI</button><div class="bmi_result ui message" v-if="bmi.result">{{ bmi.result }}</div></form></div></div><section class="orders" id="orderHistory" v-if="orderHistory.length&gt;0"><h2 class="ui header teal">Order History</h2><table class="ui table"><thead><tr><th>Mealecule Quotient</th><th>Energy</th><th>Price</th><th>Status</th><th>Date</th></tr></thead><tbody><tr v-for="o in orderHistory"><td> <span class="ui tiny label" v-for="k,v in o.mealeculeQuotientData">{{ Math.round(k) }}g: {{ v}}</span></td><td> <span class="ui tiny label teal">{{ Math.round(o.energy) }} kCal</span></td><td> <span class="price">{{o.total.value}}</span></td><td>{{o.status}}</td><td>{{o.placed.toDateString() }}</td></tr></tbody></table></section><section id="orders" v-if="orderHistory.length&gt;0"></section><div class="ui message" v-else>You have no orders yet.</div></div>