<div class="home-page"><h1 class="title">How <b>healthy </b>is your cart</h1><section class="game" id="game"><div class="ui two column grid"><div class="column"><h2 class="ui header teal">Current Progress</h2><div class="ui statistics" v-if="$root.total_coins &amp;&amp; $root.badges"><div class="ui statistic"><span class="value"> <i class="ui huge label circular bCoins">$</i></span><span class="label">{{ $root.total_coins }} Coins</span></div><div class="ui statistic" v-if="$root.badges"><span class="value"> <i class="ui huge label circular badges" :class="$root.badges"><i class="icon ui trophy"> </i></i></span><span class="label">{{ $root.badges }} Badge</span></div></div><p class="ui message warning" v-else>You have no coins. Play game to earn coins and get discounts</p><div id="orderGraph" v-if="orderHistory.length&gt;0"></div><router-link class="ui button primary cta-button" to="/game" v-if="$root.total_coins">Play Game</router-link><router-link class="ui button primary cta-button" to="/game" v-else>Earn some coins and badges</router-link></div><div class="column ui image"><img :src="require('@/assets/ame.webp')" alt="Play game with popoye"></div></div></section><section id="offer"><h2 class="ui header teal">What we offer</h2><div class="offers-cards"><div class="offer-card"><div class="image games" aria-hidden="true"></div><div class="ui header tiny">Play Games</div><p>Play a simple single player game to help Popeye win coins and avoid foodie obstacles midway. Also collect interesting badges and unlock exciting benefits, tto help you purchase your favourite products on site.</p></div><div class="offer-card"><div class="image earn-coins" aria-hidden="true"></div><div class="ui header tiny">Earn Coins</div><p>Keep collecting BCoins on site by activating your user profile, setting essential body vitals and your peference of MQ to help us serve you better. Redeem these coins during purchase and win exciting discounts on all your favourite products.</p></div><div class="offer-card"><div class="image mq"></div><div class="ui header tiny" aria-hidden="true">Mealecular Quotient</div><p>Understand the impact of buying a product by judging its Mealecular Quotient (MQ) - an info-metrics containing information in a visual representation, of quantities (%) of protein, carbohydrate, fat, fiber and caloric values for all products viewed and added to your cart. Sort and filter based on your preferred MQ.</p></div><div class="offer-card"><div class="image discount" aria-hidden="true"></div><div class="ui header tiny">Get Discounts</div><p>End of a level in our game helps you win coins achievement badges. Earning coins leads to earning fun and interesting badges which would provide amazing discounts on MRP of all products being bought by you. Bronze badge (achieved at 100 coins) gives you 5% discount on MRP of products</p></div></div></section><section class="categories" id="categories"><div class="glutenFree cat-images"><div class="ui header">Gluten Free</div></div><div class="vegan cat-images"><div class="ui header">Vegan</div></div><div class="childcare cat-images"><div class="ui header">Childcare</div></div></section><section id="categoryList"><h2 class="ui header teal">Online Supermarket</h2><div class="ui four cards"><router-link class="ui card" to="/plp/biscuits"><div class="image"><img alt="View biscuits products" :src="require('@/assets/cookie.png')"></div><div class="content"><div class="header">Biscuits</div></div></router-link><router-link class="ui card" to="/plp/chocolates"><div class="image"><img alt="View chocolates products" :src="require('@/assets/chocolate.jpg')"></div><div class="content"><div class="header">Chocolates</div></div></router-link><router-link class="ui card" to="/plp/healthDrinks"><div class="image"><img alt="View healthDrinks products" :src="require('@/assets/healthdrinks.jpg')"></div><div class="content"><div class="header">Health Drinks</div></div></router-link></div></section><section id="about"><h2 class="ui header teal">About us</h2><p>Welcome to Mealecule. Mealecule is founded by Team Popeye. </p><p> <strong>Team Popeye </strong>consists of <a href="mailto:ashwati.menon@publicissapient.com">Ashwati Menon (she/her)</a>- Product Manager, <a href="mailto:apoorvasrinivasan@publicissapient.com">Apoorva Srinivasan(she/her) </a>- Senior Associate Technology Level 1, <a href="mailto:isha.mehta@publicissapient.com">Isha Mehta (she/her) </a>- Senior Associate Technology Level 1, <a href="mailto:priya.rawat@publicissapient.com">Priya Rawat (she her)</a>- Associate Technology Level 1 and <a href="mailto:samarth.sharma@publicissapient.com">Samarth Sharma (he/him) </a>- Intern, Agile Program Management. </p><p>Being foodies, the team is happy to bring forth to you our product Mealecule - the Food Molecule and help you in your food shopping experience. We hope that through our customized info-metric MQ on products and user's cart, you would be able to gauge the impact any product you view and purchase will have on your health and bring a change in your lifestyle. </p><p>All the best with using our product and do reach out to any of us on Teams/ mail. Stay healthy, stay safe!</p></section></div>