<div class="plp"><div class="ui grid product" v-if="product.code"><div class="ui grid thirteen wide column product"><section class="five wide column product-image ui image"><img v-if="product.imageURL" :src="'/api/'+ product.imageURL" :alt="product.name"></section><section class="ten wide column product-detail"><router-link class="ui label" :to="{name:'plp', params:{id:c.code}}" v-for="c in product.categories">{{ c.code }}</router-link><h1 class="header ui">{{ product.name }}</h1><div class="ui two column grid"><div class="column"><div class="ui label teal">{{ product.manufacturer }}</div><br><div class="ui statistic"><div class="value price">{{ product.price.value }}</div></div><div class="ui tiny statistic"><div class="value weight">{{ product.weightInG}}g</div></div></div><div class="column"><MQ :nutrients="product.mq"></MQ></div><p class="product-description">{{ product.summary }}</p></div></section><div class="ui section divider"></div><section id="Nutrients"><h3 class="ui header">Mealecules information</h3><table class="ui table definition mqTable"><thead><tr><th></th><th>per 100g</th></tr></thead><tbody><tr v-for="k,v in product.mealeculeQuotientData"><td>{{ v }}</td><td>{{ k }}</td></tr></tbody></table></section><div class="ui section divider"></div></div><div class="three wide column"><div class="ui segment cta-box"><div class="ui label green ribbon">{{product.stock.stockLevelStatus}} </div><div style="margin-top:24px"><span class="mrp-label">MRP</span><span class="price">{{product.price.value }}</span></div><div v-if="$root.badges"><span class="discounted price">{{product.price.discounted}}</span><span class="ui circular label tiny bCoins">{{product.price.coins}}</span></div><small>You can get upto Rs.{{product.price.coins}} discount. </small><button class="ui button fluid primary addtocart" v-on:click="addCart()">Add to cart</button></div></div></div></div>