<div class="cart"><h1 class="ui header">My cart</h1><div class="ui message" v-if="cartitems.length==0">Your cart is empty. <router-link to="/categories">Shop for products.</router-link></div><div class="ui grid cart-grid" v-if="cartitems.length"><div><h3 class="ui header teal">{{ response.totalItems }} items</h3><ul class="carts"><li class="cartitems" v-for="c in cartitems"><div class="ui image"><img :src="'api'+c.product.imageURL"></div><div><router-link class="productname" :to="{name:'pdp', params:{code:c.product.code}}">{{ c.product.name }}</router-link><br><span>{{ c.product.weightInG}}g</span></div><div><div class="quantity ui icon buttons small basic"><div class="ui button" v-on:click="updateCart(c.entryNumber, c.quantity-1)" :class="{'disabled':c.quantity == 1}"><i class="icon minus"></i></div><div class="ui right labeled icon basic button">{{ c.quantity }}<i class="icon plus" v-on:click="updateCart(c.entryNumber, c.quantity+1)"></i></div></div></div><div><span class="label price">{{ c.totalPrice.value }}</span><span class="ui tiny teal label">{{ c.product.mealeculeQuotientData.energy}} kcal</span></div><i class="icon large trash alternate outline" v-on:click="delCartItem(c.entryNumber)"></i><div class="mq_wise"><div v-for="p in c.mq_beakers"><div class="mq_beaker" :style="p.style"> </div><small class="supersmall">{{ p.key }}  {{p.value}}g</small></div></div><small class="supersmall">nutrients per 100g</small></li><li class="cartitems total"><span></span><b>Total</b><span></span><b class="price">{{ price.carttotal }}</b></li></ul></div><div><div class="ui segment"><h4 class="ui header teal">Cart summary</h4><div class="mq-bar"><div class="mq-item" v-for="i in mq" :class="i.k" :style="'background-size: 100% '+ i.fill +'%; background-color:'+i.color"> <small class="mq-name">{{ i.k }}<br>{{ i.v }}g</small></div></div><div class="cart-summary"><span>Cart Value</span><span class="price">{{ price.carttotal }}</span><span>Tax</span><span class="price">{{ price.tax }}</span><span>Coins Discount</span><span class="price">{{ price.discount }}</span><small>You can get upto Rs. {{ price.maxDiscount }} discount. </small><router-link style="font-size:.6em" to="/game">learn more</router-link><span class="total">Total</span><span class="total price">{{price.total }}</span></div><div class="ui form user-add"><label for="pincode">Delivering to </label><input id="pincode" placeholder="Pincode" v-model="pincode"></div><button class="ui button primary cta fluid" v-on:click="placeOrder()" :disabled="orderplacing">Place Order</button></div></div></div></div>