<div class="plp"><h1 class="ui header">{{ category }}</h1><div class="ui grid" v-if="products.length"><div class="ui three wide column"><div class="ui segment filter-form form"><span class="ui header teal">Filters</span><div><div class="ui header tiny">Brand</div><div class="check-box"><span v-for="b,index in brands" :key="index"><input v-model="filterKeys" :name="b" :value="b" :id="'brand_'+index" type="checkbox"><label :for="'brand_'+index">{{b}}</label></span></div></div><div style="margin-top:24px"><div class="ui header tiny"> Mealecule</div><span class="mqRange" v-for="p in facetDatas"><label :for="p.code">{{ p.name }}:<small>{{ p.val }}g</small></label><input class="slider" :id="p.code" type="range" :min="p.minValue" :max="Math.ceil(p.maxValue)" :name="p.code" v-model="p.val" :style="p.style"></span></div></div></div><div class="ui thirteen wide column"> <div class="select-box"><label class="sort-box" for="sortby" aria-label="Sort by"><select id="sortby" v-model="sortKey"><option v-for="s in mealecules">{{s}}</option></select></label><p class="check-box"><input id="asc" type="checkbox" v-model="sortAs"><label class="checkbox" for="asc" :aria-label="(sortAs)?'Low to High' : 'High to Low'" v-html="(sortAs)?'Low to High' : 'High to Low'"></label></p></div><div class="ui link cards"><router-link class="card product-card" :to="{ name: 'pdp', params: { code: p.code }}" v-for="p in filteredList" :key="p.code"><div class="image"><img :src="'/api/'+p.imageURL" :alt="p.name"></div><div class="content"><span class="brandname">{{p.manufacturer}}</span><div class="header product-name">{{ p.name }}</div><div class="meta"><MQ :nutrients="p.mq" v-if="p.mq"></MQ><div class="ui label tiny">{{ category}}</div><div class="price-row"><span :class="($root.badges)?'mrp':'price'">{{ p.price.value }}</span><span class="price" v-if="$root.badges">{{ p.price.discounted }}</span><span class="ui circular tiny label bCoins" v-if="$root.badges">{{ p.price.coins }}</span><div style="height:50px" v-else></div></div></div><button class="cta-button ui fluid primary button" v-on:click="addCart($event, p.code)"> Add to cart</button></div></router-link></div></div></div><div class="ui message" v-if="products.length==0">Sorry no products found </div></div>